<template>
  <el-dialog-self
    title=""
    width="600"
    :custom-class="customeClass"
    :visible.sync="dvisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :destroy-on-close="true"
    append-to-body
    @close="cancelHandler"
  >
    <div class="container-cls">
      <div class="operation-cls">
        <el-form :inline="true"  size="small" class="demo-form-inline">
          <el-form-item>
            <el-input style="width:480px;" v-model="uploadfile.fileName" v-if="fileList.length == 0" />
          </el-form-item>
          <el-form-item :id="picker">
            <el-button style="margin-left:5px" type="success"  class="IDpiker" plain >
               
            </el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="list-container-cls">
        <ul class="ul-cls">
          <div :key="index" v-for="(file, index) in fileList">
            <el-row>
              <el-col :span="20" class="li-left-cls">
                <li :title="file.fileName" class="list-item-cls">
                  {{ file.fileName }}
                </li>
              </el-col>
              <el-col :span="4" class="li-rigth-cls">
                <el-tag
                  closable
                  @close="cancelFile(file.md5, file.id)"
                  v-if="file.tip == 1"
                  >...
                </el-tag>
                <el-tag
                  closable
                  @close="deleteFile(file.md5, file.id)"
                  v-if="file.tip == 2"
                  >
                </el-tag>
                <el-tag
                  closable
                  type="danger"
                  @close="deleteErrorFile(file.md5, file.id)"
                  v-else-if="file.tip == 3"
                  >
                </el-tag>
              </el-col>
            </el-row>
            <el-progress
              class="progress-cls"
              :percentage="file.percentage"
              v-if="file.tip == 1"
              :text-inside="true"
              :stroke-width="10"
            >
            </el-progress>
          </div>
        </ul>
      </div>
    </div>

    <div slot="footer">
      <el-button type="primary"  size="small" @click="commitHandler"></el-button>
      <el-button plain  size="small" @click="cancelHandler"></el-button>
    </div>
  </el-dialog-self>
</template>
<script>
import $ from "jquery";
import WebUploader from "webuploader";
import collect from "collect.js";

export default {
  name: "Upload",
  data() {
    return {
      dvisible: this.dialogVisible, //changeVisible
      fileList: [],
      singleFileName: "",
      singleFile: null,
      singleTip: 1,
      uploadfile: {
        fileName: null,
        fileId: null,
      },
      percentage: 0,
      uploader: null,
      picker: 'picker' + Date.now()
    };
  },
  props: {
    dialogVisible: { type: Boolean, default: false },
    multiple: { type: Boolean, default: false }, //
    fileType: { type: String, default: "document" }, //'zip',document','image','video'
    isVector: { type: Boolean, default: false }, //
    fileServer: { type: String, required: true }, //
  },
  computed: {
    customeClass() {
      return this.multiple == false
        ? "single-container-cls"
        : "muilt-container-cls";
    },
  },
  mounted() {

  },
  methods: {
    changeVisible(flag) {
      this.dvisible = flag;
    },
    //
    deleteErrorFile(md5, id) {
      this.uploader.removeFile(id, true);
      this.fileList = collect(this.fileList)
        .filter((file) => {
          return file.md5 !== md5;
        })
        .all();
    },
    //
    deleteFile(md5, id) {
      this.uploader.removeFile(id, true);
      this.fileList = collect(this.fileList)
        .filter((file) => {
          return file.md5 !== md5;
        })
        .all();
    },
    //
    cancelFile(md5, id) {
      this.uploader.cancelFile(id);
      this.fileList = collect(this.fileList)
        .filter((file) => {
          return file.md5 !== md5;
        })
        .all();
    },
    cancelHandler() {
      this.dvisible = false;
      this.fileList = [];
      if (this.uploader != null) {
        this.uploader.reset();
      }
    },
    commitHandler() {
      let data = collect(this.fileList)
        .filter((file) => {
          return file.tip == 2;
        })
        .all();
      this.$emit("upLoadSuccess", data);
      this.fileList = [];
      this.dvisible = false;
      if (this.uploader != null) {
        this.uploader.reset();
      }
    },
    refreshFileState(file, localFile, success) {
      if (file != null && file["fileName"] != null) {
        let fileInfo = collect(this.fileList).firstWhere(
          "md5",
          localFile["md5"]
        );
        if (fileInfo) {
          fileInfo.fileId = file["fileId"];
          fileInfo.tip = success == true ? 2 : 3;
          fileInfo.percentage = 100;
        }
      }
    },
    initUpload() {
      this.uploadfile.fileName = null;
      this.uploadfile.fileId = null;
      this.percentage = 0;

      var that = this;
      that.fileList = [];
      var multiple = this.multiple;

      //
      var fileSizeLimit = 20 * 1024 * 1024 * 1024;
      //
      var fileSingleSizeLimit = 15 * 1024 * 1024 * 1024;
      //
      var fileNumLimit = 300;
      //
      var chunkSize = 4 * 1024 * 1024;

      var geoflag = 0;
      var accept = null;
      if (this.isVector) {
        geoflag = 1;
        accept = {
          title: "zip,rar",
          extensions: "zip,rar",
          mimeTypes: ".zip,.rar",
        };
      } else {
        if (this.fileType == "zip") {
          accept = {
            title: "zip,rar",
            extensions: "zip,rar",
            mimeTypes: ".zip,.rar",
          };
        } else if (this.fileType == "document") {
          accept = {
            title: "doc,docx,txt,ppt,pptx,xls,xlsx,pdf",
            extensions: "doc,docx,txt,ppt,pptx,xls,xlsx,pdf",
            mimeTypes: ".doc,.docx,.txt,.ppt,.pptx,.xls,.xlsx,.pdf",
          };
        } else if (this.fileType == "image") {
          accept = {
            title: "png,jpg,gif,tiff,tif",
            extensions: "png,jpg,gif,tiff,tif",
            mimeTypes: ".png,.jpg,.gif,.tiff,.tif",
          };
        } else if (this.fileType == "video") {
          accept = {
            title: "mp4,avi,flv,mov,mkv,3gp",
            extensions: "mp4,avi,flv,mov,mkv,3gp",
            mimeTypes: ".mp4,.avi,.flv,.mov,.mkv,.3gp",
          };
        }
      }

      var fileServer = this.fileServer;

      var fileMd5; //

      // 
      var fileCount = 0;

      // 
      var fileSize = 0;
      // var fileName; //
      var oldJindu; // 
      var count = 0; //
      var filesArr = new Array(); // push
      var map = {}; //keyidvalue
      if (!WebUploader.Uploader.support()) {
        alert(
          "Web Uploader IE flash html5"
        );
        throw new Error(
          "WebUploader does not support the browser you are using."
        );
      }

      WebUploader.Uploader.unRegister("beforeSendFile");
      WebUploader.Uploader.unRegister("beforeSend");
      WebUploader.Uploader.unRegister("afterSendFile");
      WebUploader.Uploader.register({
        name: "beforeSendFile",
        "before-send-file": function(file) {
          // var that = this;
          var deferred = WebUploader.Deferred();

          new WebUploader.Uploader()
            .md5File(file, 0, 10 * 1024 * 1024)
            .progress(function() {})
            .then(function(val) {
              fileMd5 = val;
              file.md5 = fileMd5;

              let fileInfo = collect(that.fileList).firstWhere(
                "id",
                file["id"]
              );
              fileInfo.md5 = fileMd5;
              $.ajax({
                type: "POST",
                url: fileServer + "/file/exist.do", //ajax
                data: {
                  fileMd5: fileMd5, //
                },
                cache: false,
                async: false, // js
                timeout: 50000, //todo 
                dataType: "json",
                success: function(result) {
                  if (result.status == 200) {
                    if (result.data.oldFile) {
                      that.refreshFileState(result.data, file, true);

                      that.uploader.skipFile(file);
                      file.exitFlag = true;
                    }
                    deferred.resolve();
                  }
                },
              });
            });

          // fileName = file.name;
          return deferred.promise();
        },
      });

      WebUploader.Uploader.register({
        name: "beforeSend",
        "before-send": function(block) {
          var deferred = WebUploader.Deferred();

          var temFile = block.file;
          $.ajax({
            type: "POST",
            url: fileServer + "/checkChunk/check.do",
            data: {
              param: "checkChunk",
              fileName: temFile.name,

              fileMd5: temFile.md5,
              chunk: block.chunk, //
              chunkSize: block.end - block.start, //
            },
            cache: false,
            async: false,
            timeout: 5000,
            dataType: "json",
            success: function(result) {
              if (result.status == 200) {
                if (result.data.ifExist) {
                  deferred.reject();
                } else {
                  deferred.resolve();
                }
              } else if (result.status == 500) {
                alert("!");
              }
            },
          });

          this.owner.options.formData.fileMd5 = fileMd5;
          deferred.resolve();
          return deferred.promise();
        },
      });

      WebUploader.Uploader.register({
        name: "afterSendFile",
        "after-send-file": function(file) {
          if (file.exitFlag) {
            count++; // count+1
            if (count <= filesArr.length - 1) {
              that.uploader.upload(filesArr[count].id); //
            }
            return;
          }
          //
          $.ajax({
            type: "POST",
            url: fileServer + "/checkChunk/check.do", //ajax
            data: {
              param: "mergeChunks",
              fileName: file.name,
              fileMd5: file.md5,
              geoflag: geoflag,
            },
            success: function(result) {
              if (result.status == 200) {
                that.refreshFileState(result["data"], file, true);
              } else {
                that.refreshFileState(result["data"], file, false);
              }
              count++; // count+1
              if (count <= filesArr.length - 1) {
                that.uploader.upload(filesArr[count].id); //
              }
            },
          });
        },
      });

      this.uploader = WebUploader.create({
        auto: true, //

        disableGlobalDnd: true,
        chunked: true, //
        chunkSize: chunkSize, // 5M
        chunkRetry: 8, //
        threads: 8, //[3]
        fileNumLimit: fileNumLimit,
        duplicate: false, //true
        prepareNextFile: true, //
        swf: "../Uploader.swf", // swf
        server: fileServer + "/filechunk/save.do", // 
        fileSizeLimit: fileSizeLimit, //, 
        fileSingleSizeLimit: fileSingleSizeLimit, // , 
        pick: {
          id: "#"+this.picker, //iddivid
          multiple: multiple, //true
        },
        resize: false, //image, jpeg
        accept: accept,
      });
      //
      this.uploader.on("fileQueued", function(file) {
        file = collect(that.uploader.getFiles()).firstWhere("id", file["id"]);
        if (that.multiple == false) {
          let oldFiles = that.uploader.getFiles();
          if (oldFiles.length > 2) {
            oldFiles.slice(0, oldFiles - 1).map((file) => {
              that.uploader.removeFile(file);
            });
          }
          that.fileList = [];
        }

        // 
        if (file.size > fileSingleSizeLimit) {
          alert(
            "" +
              fileSingleSizeLimit / (1024 * 1024 * 1024) +
              "G"
          );
          return false;
        }

        if (file.size + fileSize > fileSizeLimit) {
          alert(
            "" +
              fileSizeLimit / (1024 * 1024 * 1024) +
              "G"
          );
          return false;
        }

        if (fileCount + 1 > fileNumLimit) {
          alert("" + fileNumLimit + "");
          return false;
        }

        // filesArr.push(file);
        if (file.name != null) {
          that.fileList.push({
            fileName: file.name,
            percentage: 0,
            id: file.id,
            fileId: "", //fileId
            md5: "",
            tip: 1,
          });
        }

        $.ajax({
          type: "POST",
          url: fileServer + "/filechunk/progress.do", //
          data: {
            fileName: file.md5, //
          },
          cache: false,
          async: true, // 
          dataType: "json",
          success: function(data) {
            //
            if (data.status == 200) {
              if (data.data > 0) {
                //
                oldJindu = data.data / 100;

                //map
                map[file.id] = oldJindu;
              }

              fileCount++;
              fileSize += file.size;

              // $("#jindutiao").val(data.data);
            } else if (status == 500) {
              alert("");
            }
          },
        });
        // uploader.stop(true);
      });

      this.uploader.on("uploadProgress", function(file, percentage) {
        let uploadingFile = collect(that.fileList).firstWhere("md5", file.md5);

        if (uploadingFile != null) {
          uploadingFile.percentage = parseInt(percentage * 100);
        }
      });

      this.uploader.on("uploadSuccess", function(file) {
        let uploadingFile = collect(that.fileList).firstWhere("md5", file.md5);
        if (uploadingFile != null) {
          // uploadingFile.tip = 2
        }
      });

      this.uploader.on("uploadError", function(file) {
        if (file.name != null) {
          let xxfile = collect(that.fileList).firstWhere("md5", file.md5);
          if (xxfile) {
            xxfile.file = file;
            xxfile.tip = 3;
          }
        }
      });
      this.uploader.on("uploadComplete", function() {});
    },
  },
  watch: {
    dvisible: {
      handler(value) {
        if (value == true) {
          this.$nextTick(() => {
            this.initUpload();
          });
        }
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>
